<template>
  <div style="width: 50%; margin: 5px auto">
    <div class="card" style="margin-bottom: 100px">
      <div style="font-weight: bold; font-size: 24px; margin-bottom: 50px">/</div>
      <el-form :model="form" label-width="100px" style="padding-right: 50px" :rules="rules" ref="formRef">
        <el-form-item label="" prop="title">
          <el-input v-model="form.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="descr">
          <el-input type="textarea" v-model="form.descr" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="cover">
          <div style="display: flex;">
            <el-upload
                :action="'http://62.234.62.75:9090' + '/files/upload'"
                :headers="{ token: user.token }"
                list-type="picture"
                :on-success="handleCoverSuccess"
            >
              <el-button type="primary"></el-button>
            </el-upload>

            <el-button type="primary" @click="generateCover" style="margin-left: 5px"></el-button>
          </div>

          <canvas v-if="showCanvas" ref="coverCanvas" width="300" height="150" style="margin-top: 5px;"></canvas>
        </el-form-item>

        <el-form-item label="" prop="categoryId">
          <el-select v-model="form.categoryId" style="width: 100%">
            <el-option v-for="item in categoryList" :key="item.id" :value="item.id" :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="tags">
          <el-select v-model="tagsArr" multiple filterable allow-create default-first-option style="width: 100%">
            <el-option value="Java"></el-option>
            <el-option value="Docker"></el-option>
            <el-option value="Redis"></el-option>
            <el-option value="Mysql"></el-option>
            <el-option value="SpingBoot"></el-option>
            <el-option value="vue"></el-option>
            <el-option value="LeetCode"></el-option>
            <el-option value=""></el-option>
<!--            <el-option value=""></el-option>-->
          </el-select>
        </el-form-item>
<!--        <el-form-item label="" prop="content">-->
<!--          <div id="editor"></div>-->
<!--        </el-form-item>-->
<!--        style="height: 200px"-->
        <el-form-item label="" prop="content" style="max-height: 300px; overflow-y: auto">
          <mavon-editor ref="md" v-model="form.content" :ishljs="true" @imgAdd="imgAdd"/>
        </el-form-item>


      </el-form>
      <div style="text-align: center"><el-button type="primary" size="medium" style="width: 100px" @click="save"> </el-button></div>

    </div>

    <Footer />
  </div>
</template>

<script>
// import E from "wangeditor";
import hljs from "highlight.js";
import axios from "axios";
import Footer from "@/components/Footer";
import markdownIt from 'markdown-it';
import mavonEditor from 'mavon-editor';


export default {
  components: {
    Footer
  },
  name: "NewBlog",
  data() {
    return {
      form: {},
      user: JSON.parse(localStorage.getItem('xm-user') || '{}'),
      rules: {},
      tagsArr: [],
      categoryList: [],
      editor: null,
      blogId: this.$route.query.blogId,
      imageDataURL:'',
      showCanvas: false, //  canvas
    }
  },
  mounted() {

    this.$request.get('/category/selectAll').then(res => {
      this.categoryList = res.data || []
    })

    this.$request.get('/blog/selectById/' + this.blogId).then(res => {
      this.form = res.data || {}
      if (this.form.id) {
        this.tagsArr = JSON.parse(this.form.tags || '[]')
        // setTimeout(() => {
        //   this.editor.txt.html(this.form.content)
        // }, 0)
      }
    })
    // this.setRichText()
  },
  methods: {
    generateCover() {

      console.log(this.tagsArr)
      if(this.tagsArr.length === 0){
        this.$notify.error("!")
        return
      }
      this.showCanvas = true;
      this.$nextTick(() => {
        const canvas = this.$refs.coverCanvas;
        const context = canvas.getContext("2d");
        context.clearRect(0, 0, canvas.width, canvas.height);

        // 
        const randomColor = Math.random() < 0.5 ? "#007acc" : "#00a854"; // 



        // 
        context.fillStyle = randomColor;
        context.fillRect(0, 0, canvas.width, canvas.height);
        var randomNumber = Math.floor(Math.random() * this.tagsArr.length);
        // 
        const text = "#"+this.tagsArr[randomNumber];
        context.font = "bold 48px Arial"; // 
        context.fillStyle = "#000"; // 
        // 
        const textMetrics = context.measureText(text);
        const textWidth = textMetrics.width;
        const textHeight = textMetrics.actualBoundingBoxAscent + textMetrics.actualBoundingBoxDescent;

// 
        const centerX = canvas.width / 2 - textWidth / 2;
        const centerY = canvas.height / 2 + textHeight / 2;
        context.fillText(text, centerX, centerY);
        // DataURL
        this.imageDataURL = canvas.toDataURL("image/png");
      });
    },

    uploadGeneratedImage(imageDataURL) {
      const imageData = imageDataURL.split(',')[1];
      const binaryImageData = atob(imageData);
      const arrayBuffer = new ArrayBuffer(binaryImageData.length);
      const uint8Array = new Uint8Array(arrayBuffer);
      for (let i = 0; i < binaryImageData.length; i++) {
        uint8Array[i] = binaryImageData.charCodeAt(i);
      }
      //  File  FormData
      const file = new File([new Blob([arrayBuffer], { type: 'image/png' })], 'image.png', { type: 'image/png' });
      const formData = new FormData();
      formData.append('file', file);
      axios({
        url: 'http://62.234.62.75:9090/files/editor/upload',
        method: 'post',
        data: formData,
        headers: {'Content-Type': 'multipart/form-data'},
      }).then((res) => {
        // Markdown

        var url = res.data.data[0].url

        this.form.cover = url
        // this.$notify.error(this.form.cover)
        // console.log(""+url);
        // $vm.$img2Url(pos, url);
      });
    },

    async save() {   //   
      if(this.imageDataURL !== ''){
        console.log("")
        this.uploadGeneratedImage(this.imageDataURL);
        await  new Promise(resolve => setTimeout(resolve,200));
      }
      console.log("cishide "+this.form.cover)
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.form.tags = JSON.stringify(this.tagsArr)  // jsonjson
          // this.form.content = this.editor.txt.html()
          this.$request({
            url: this.form.id ? '/blog/update' : '/blog/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 
              if(!this.form.id){
                // console.log(res.data)
                this.$message.success('')
                setTimeout(() => {
                  location.href = "/front/blogDetail?blogId="+parseInt(res.data.id)
                },500)
              }else{
                this.$message.success('')
                setTimeout(() => {
                  location.href = "/front/blogDetail?blogId="+this.form.id
                }, 500)
              }
            } else {
              this.$message.error(res.msg)  // 
            }
          })
        }
      })
    },
    handleCoverSuccess(res) {
      this.form.cover = res.data
    },
    setRichText() {
      this.$nextTick(() => {
        this.editor = new E(`#editor`)
        this.editor.highlight = hljs
        this.editor.config.uploadImgServer = 'http://62.234.62.75:9090' + '/files/editor/upload'
        this.editor.config.uploadFileName = 'file'
        this.editor.config.uploadImgHeaders = {
          token: this.user.token
        }
        this.editor.config.uploadImgParams = {
          type: 'img',
        }
        this.editor.config.zIndex = 0
        this.editor.create()  // 
      })
    },
    // @imgAdd event
      imgAdd(pos, $file) {
      let $vm = this.$refs.md
      // ..
      const formData = new FormData();
      formData.append('file', $file);
      axios({
        url: 'http://62.234.62.75:9090/files/editor/upload',
        method: 'post',
        data: formData,
        headers: {'Content-Type': 'multipart/form-data'},
      }).then((res) => {
        // Markdown
        var url = res.data.data[0].url
        // console.log(url)
        // const markdownText = `http://62.234.62.75:9090/files/1705996022610-.jpg`;
        $vm.$img2Url(pos, url);
      })
    },
  }
}
</script>

<style scoped>
/*  */
.el-form-item__content .mavon-editor {
  max-height: 300px !important;
  overflow-y: auto !important; /*  */
}
</style>